

#hero {
  //background-image: url("/assets/img/lightbulb_pendulum_blue.jpg")
  background-image: url("/assets/img/lightbulb_pendulum_orange.jpg");
  //background-image: url("/assets/img/lightbulb_pendulum_red.jpg")
  //background-image: url("/assets/img/lightbulb_question_orange.jpg")
  background-size: cover;
  background-position: center center;
  height: 85vh;
  max-height: 80vw;
  min-height: 360px;
  text-align: center;

  .container {
    height: 100%; } }

#hero--inner {
  display: flex;
  flex-direction: column;
  height: calc(100% - 2em);
  justify-content: space-between; }

#hero--title {
  background-color: rgba($color-white, 0.6);
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 0.5em 1em 1em;
  margin-top: 3em;
  @include breakpoint($medium) {
    width: 75%; }
  @include breakpoint($extra-small) {
    background: radial-gradient(circle farthest-side, rgba($color-white, 1), rgba($color-white, 0.8), rgba($color-white, 0.6), rgba($color-white, 0.2), rgba($color-white, 0)); } }

#site-title {
  margin: 0; }

#site-logo {
  margin: 0 auto;
  max-width: 8em; }

#site-subtitle {
  font-size: $type-size-5;
  font-family: $global-font-family;
  margin: 0; }

#site-byline {
  padding: 0.5em;
  height: 2rem;
  vertical-align: middle;
  margin: 0 auto;
  color: $color-white;
  font-size: $type-size-5;
  font-family: $global-font-family;
  font-weight: 300;
  margin-bottom: 1rem;
  width: 75%;
  img {
    height: 0.8em;
    padding-bottom: 0.1em;
    vertical-align: middle; }
  path {
    stroke: $color-white; } }

.giv3-logo {
  -webkit-filter: brightness(0) invert(1);
  filter: brightness(0) invert(1);
  //-webkit-filter: drop-shadow(0 1px 1px rgba(#333, 0.8))
 }  //filter: drop-shadow(0 1px 1px rgba(#333, 0.8))

#section--intro h1 {
  margin-top: 1em;
  margin-bottom: 1em;
  font-family: $header-font-family; }


.content-box {
  padding: 1.25em 0; }

.raised-box {
  box-shadow: 0 3px 5px rgba(#000, 0.2);
  border-radius: 8px;
  padding: 1.5em;
  margin-bottom: 2em;
  & > :first-child {
    margin-top: 0; }
  & > :last-child {
    margin-bottom: 0; } }

#cta-appeal {
  //font-weight: bold
  margin: 2rem 0 1.5rem;
  p {
    font-style: italic; }
  h3 {
    margin: 0.5em 0;
    //color: $color-light-orange
    font-family: $header-font-family; } }

#cta-links {
  text-align: center;
  p {
    font-weight: bold;
    margin-bottom: 0.5em; } }

.fine-print {
  color: $light-gray;
  display: block;
  line-height: 1.1; }

#main-footer {
  background-color: $color-red;
  color: $color-white;
  padding: 1em 0; }

h2.section--header {
  background-color: $color-red;
  border-radius: 8px;
  padding: 1.75em 1.5em 1.25em;
  color: #fff;
  background-image: url("/assets/img/logo-icon-white.svg");
  background-position: center right 1.5em;
  background-repeat: no-repeat;
  background-size: 2em 2em;
  margin-top: 2rem; }

#section--library {
  padding-bottom: 4rem;
  .row {
    justify-content: stretch;
    margin-bottom: 1rem; }
  .raised-box {
    height: 100%;
    margin-bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    p {
      font-size: $type-size-6; } } }

#main-footer {
  .container {
    display: flex;
    justify-content: space-between;
    align-items: center; } }

.copyright a {
  text-decoration: none;
  &:hover {
    color: mix(#000, $color-light-orange, 20%); } }
