
#main-nav {
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  position: static; }

.main-nav-contents {
  display: flex;
  justify-content: flex-end;
  align-items: center; }

.nav-item {
  a {
    display: block;
    color: $text-color;
    //border: 1px solid rgba($color-blue,0.6)
    border-radius: 1em;
    line-height: 1;
    vertical-align: middle;
    padding: 0.5em 1em 0.4em;
    margin-left: 0.5em;
    margin-top: 0.5em;
    background-color: rgba(#fff, 0.6);
    font-size: $type-size-6;
    &:hover {
      background-color: rgba($color-blue,1);
      color: $color-white; } } }
