/* ==========================================================================
   BUTTONS
   @mixin ========================================================================= */

/*
   Default button
   @mixin ========================================================================= */

.btn {
  /* default */
  display: inline-block;
  margin-bottom: 0.5em;
  padding: 0.75em 1.5em;
  font-family: $sans-serif;
  font-size: $type-size-6;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  border-width: 0;
  cursor: pointer;
  border-radius: 100px;

  .icon {
    margin-right: 0.5em; }


  .icon + .hidden {
    margin-left: -0.5em /* override for hidden text*/; }


  /* button colors */
  $buttonColors: (primary: $color-light-orange, success: $success-color, warning: $warning-color, danger: $danger-color, info: $info-color, default: $lighter-gray, facebook: $facebook-color, twitter: $twitter-color, linkedin: $linkedin-color);
  @each $buttonColor, $color in $buttonColors {
    &--#{$buttonColor}, &--#{$buttonColor} {
      @include yiq-contrasted($color);
      box-shadow: rgba(#000, 0.2) 0 3px 2px -2px;
      @if ($buttonColor == inverse) {
        border: 1px solid $border-color; }

      @if ($buttonColor == light-outline) {
        border: 1px solid #fff; }
      &:visited, &:link {
        @include yiq-contrasted($color); }
      &:hover {
        @include yiq-contrasted(mix(#000, $color, 20%)); }
      &:active {
        box-shadow: none;
        -moz-transform: translate(0, 2px);
        -ms-transform: translate(0, 2px);
        -o-transform: translate(0, 2px);
        -webkit-transform: translate(0, 2px);
        transform: translate(0, 2px); } } }

  &--blue--inverse {
    box-shadow: rgba(#000, 0.2) 0 3px 2px -2px;
    border: 1px solid $color-blue;
    color: $color-blue;
    background-color: $color-white;
    &:visited, &:link {
      color: $color-blue; }
    &:hover {
      background-color: #ECF5FA; }
    &:active {
      box-shadow: none;
      -moz-transform: translate(0, 2px);
      -ms-transform: translate(0, 2px);
      -o-transform: translate(0, 2px);
      -webkit-transform: translate(0, 2px);
      transform: translate(0, 2px); } }


  /* fills width of parent container */
  &--block {
    display: block;
    width: 100%;

    + .btn--block {
      margin-top: 0.25em; } }



  /* disabled */
  &--disabled {
    pointer-events: none;
    cursor: not-allowed;
    filter: alpha(opacity=65);
    box-shadow: none;
    opacity: 0.65; }


  /* extra large button */
  &--x-large {
    font-size: $type-size-4; }


  /* large button */
  &--large {
    font-size: $type-size-5; }


  /* small button */
  &--small {
    font-size: $type-size-7;
    padding: 0.5em 1em; }

  &--copy {
    position: relative;
    &:before {
      content: "copied to clipboard";
      position: absolute;
      background-color: $color-light-orange;
      border-radius: 5px;
      padding: 0.25em 0.5em;
      font-size: 0.75em;
      left: 50%;
      transform: translateX(-50%);
      white-space: nowrap;
      bottom: calc(100% + 7px);
      opacity: 0;
      transition: opacity 0.3s ease-out; }
    &:after {
      opacity: 0;
      content: "";
      position: absolute;
      width: 0;
      height: 0;
      left: 50%;
      transform: translateX(-50%);
      bottom: 100%;
      border-left: 7px solid transparent;
      border-right: 7px solid transparent;
      border-top: 7px solid $color-light-orange;
      transition: opacity 0.3s ease-out; }
    &.copied::before, &.copied::after {
      opacity: 100; } } }


